<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :inline="true"
          class="demo-form-inline form_container_header"
          size="mini"
        >
          <el-form-item label="用户ID" :required="true">
            <el-input v-model.number="requestParam.uid"></el-input>
          </el-form-item>
          <el-form-item label="时间范围">
            <template>
              <div class="block">
                <el-date-picker
                  v-model="requestParam.time_range"
                  type="datetimerange"
                  align="right"
                  value-format="timestamp"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  :default-time="['12:00:00', '08:00:00']"
                >
                </el-date-picker>
              </div>
            </template>
          </el-form-item>
          <el-form-item label="变更类型">
            <template>
              <el-select v-model="requestParam.changeType" placeholder="请选择">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </template>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getList">查询</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="showDataTab">
        <el-table
          :data="page.list"
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column prop="timestamp" label="记录时间" align="center">
            <template slot-scope="scope">
              {{ scope.row.timestamp | formatDate }}
            </template>
          </el-table-column>
          <el-table-column prop="uid" label="用户ID" align="center">
          </el-table-column>
          <el-table-column prop="post_totalGold" label="总金币" align="center">
          </el-table-column>
          <el-table-column
            prop="type"
            :formatter="getType"
            label="变更类型"
            align="center"
          >
          </el-table-column>
          <el-table-column prop="diff" label="金额"  align="center">
              <template slot-scope="scop">
                <span :class="addColor(scop.row.diff)">{{scop.row.diff}}</span>
              </template>
          </el-table-column>
          <el-table-column prop="pump" label="税收" align="center">
          </el-table-column>
          <el-table-column prop="remark" label="备注" align="center">
          </el-table-column>
           <el-table-column  label="详情" align="center">
               <el-button type="primary" plain disabled>详情</el-button>
          </el-table-column>
        </el-table>
      </div>
      <div class="pageBar">
        <el-pagination
          background
          hide-on-single-page
          layout="prev, pager, next"
          @next-click="nextPage"
          @current-change="thisPage"
          @prev-click="prePage"
          :current-page="page.current_page"
          :page-size="page.page_size"
          :total="page.total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      requestParam: {
        uid: null,
        time_range: null,
        changeType: null
      },
      goldchange: {},
      responseData: [],
      page: {
        list: [],
        current_page: 1,
        total: 0,
        page_size: 8
      },
      user: this.$store.getters["user/getUser"],
      //0.游戏 1.充值 2.送收(负:赠送,正:收到) 3.奖励 4.保险箱(正:金币到=>保险箱;负:保险箱=>金币)
      options: [
        {
          value: null,
          label: "全部"
        },
        {
          value: 0,
          label: "游戏"
        },
        {
          value: 1,
          label: "充值"
        },
        {
          value: -2,
          label: "赠送"
        },
        {
          value: 2,
          label: "收到"
        },
        {
          value: 3,
          label: "奖励"
        }
      ]
    };
  },
  methods: {
    getList() {
      var that = this;
      //that.responseData = [];
      let query = {
        uid: this.requestParam.uid,
        time_range: this.requestParam.time_range || [0, 0],
        changeType: this.requestParam.changeType,
        page: {
          current_page: this.page.current_page,
          page_size: this.page.page_size
        }
      };
      this.$api.statistic.changeGoldlist(query).then(res => {
        if (res.data.code === 200) {
          //that.responseData = res.data.data.list;
          that.page.total = res.data.data.total;
          that.page.list = res.data.data.list;
          // that.page.list.map((record, index) => {
          //   record.timestamp;
          //   that.page.list[index];
          // });
        } else {
          this.$message({
            showClose: true,
            message: "获取列表失败",
            type: "error"
          });
        }
      });
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 4 === 1) {
        return "warning-row";
      } else if (rowIndex % 4 === 3) {
        return "success-row";
      }
      return "";
    },
    prePage() {
      if (this.page.current_page > 0) {
        this.page.current_page--;
        this.getList();
      }
    },
    thisPage(cpage) {
      this.page.current_page = cpage;
      this.getList();
    },
    nextPage() {
      if (
        this.page.current_page <
        Math.ceil(this.page.total / this.page.page_size)
      ) {
        this.page.current_page++;
        this.getList();
      }
    },
    getType(row, column) {
      let val = "";
      this.options.forEach(item => {
        if (item.value === row.type) {
          val = item.label;
        }
      });
      return val;
    },
    addColor(diff){
        if(diff=>0){
            return 'greenCor'
        }else{
            return 'redCor'
        }
    }
  },
  filters: {
    formatDate: function(value) {
      return moment(value).format("YYYY/MM/DD hh:mm:ss a");
    }
  }
};
</script>

<style lang="less" scoped>
.pageBar {
  width: 100%;
  height: 100px;
  // background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
}
.innerForm {
  padding: 2% 5%;
  border: #eef1f6 1px solid;
  margin-left: -100px;
  //box-shadow: #eef1f6 0px 0px 10px;
}
.innerForm_item {
  margin: 10px 0;
}
.dialog_input_small {
  width: 35%;
  margin: 0 20px;
}
.minInput {
  width: 40%;
}
.greenCor{
    font-size: larger;
    color:green;
}
.redCor{
    color:red;
}
</style>



